import React, { useState, useEffect } from "react";
import MovieList from "../MovieList/MovieList";
import Navbar from "../NavBar/Navbar";
import Banner from "../Banner/Banner";

const requests = {
  fetchTrending: `https://api.themoviedb.org/3/trending/all/week?api_key=379f3ed664014cbf27c8eafce6dedfeb&language=en-US`,
  fetchNetflixOriginals: `https://api.themoviedb.org/3/discover/tv?api_key=379f3ed664014cbf27c8eafce6dedfeb&with_network=123`,
  fetchTopRated: `https://api.themoviedb.org/3/movie/top_rated?api_key=379f3ed664014cbf27c8eafce6dedfeb&language=en-US`,
  fetchActionMovies: `https://api.themoviedb.org/3/discover/movie?api_key=379f3ed664014cbf27c8eafce6dedfeb&with_genres=28`,
  fetchComedyMovies: `https://api.themoviedb.org/3/discover/movie?api_key=379f3ed664014cbf27c8eafce6dedfeb&with_genres=35`,
  fetchHorrorMovies: `https://api.themoviedb.org/3/discover/movie?api_key=379f3ed664014cbf27c8eafce6dedfeb&with_genres=27`,
  fetchRomanceMovies: `https://api.themoviedb.org/3/discover/movie?api_key=379f3ed664014cbf27c8eafce6dedfeb&with_genres=10749`,
  fetchDocumentaries: `https://api.themoviedb.org/3/discover/movie?api_key=379f3ed664014cbf27c8eafce6dedfeb&with_genres=99`,
};

async function fetchAPI(url) {
  const path = await fetch(url);
  const data = await path.json();
  return data;
}

const Browse = () => {
  //  API
  const [fetchTrenDing, setFetchTrenDing] = useState();
  const [fetchNetflixOriginals, setFetchNetflixOriginals] = useState();
  const [fetchTopRated, setFetchTopRated] = useState();
  const [fetchActionMovies, setFetchActionMovies] = useState();
  const [fetchComedyMovies, setFetchComedyMovies] = useState();
  const [fetchHorrorMovies, setFetchHorrorMovies] = useState();
  const [fetchRomanceMovies, setFetchRomanceMovies] = useState();
  const [fetchDocumentaries, setFetchDocumentaries] = useState();

  useEffect(() => {
    const fetchMovies = async () => {
      setFetchTrenDing(await fetchAPI(requests.fetchTrending));
      setFetchNetflixOriginals(await fetchAPI(requests.fetchNetflixOriginals));
      setFetchTopRated(await fetchAPI(requests.fetchTopRated));
      setFetchActionMovies(await fetchAPI(requests.fetchActionMovies));
      setFetchComedyMovies(await fetchAPI(requests.fetchComedyMovies));
      setFetchHorrorMovies(await fetchAPI(requests.fetchHorrorMovies));
      setFetchRomanceMovies(await fetchAPI(requests.fetchRomanceMovies));
      setFetchDocumentaries(await fetchAPI(requests.fetchDocumentaries));
    };
    fetchMovies();
  }, []);

  return (
    <section className="head_post">
      <Navbar />
      <Banner />

      <div className="box-movie">
        <MovieList movies={fetchNetflixOriginals} />
        <MovieList movies={fetchTrenDing} titles={"Xu hướng"} />
        <MovieList movies={fetchTopRated} titles={"Xếp hạng cao"} />
        <MovieList movies={fetchActionMovies} titles={"Hành động"} />
        <MovieList movies={fetchComedyMovies} titles={"Hài"} />
        <MovieList movies={fetchHorrorMovies} titles={"Kinh dị"} />
        <MovieList movies={fetchRomanceMovies} titles={"Lãng mạn"} />
        <MovieList movies={fetchDocumentaries} titles={"Tài liệu"} />
      </div>
    </section>
  );
};

export default Browse;
