import React from "react";

const MovieTrending = (props) => {
  return (
    <div className="show_cons_1">
      <h2>{props.itemName}</h2>

      {props.items ? (
        <div className="imageAlbum">
          {props.items.results.map((item) => {
            return (
              <div className="list_image">
                <img
                  src={`https://image.tmdb.org/t/p/original${
                    props.itemName ? item.backdrop_path : item.poster_path
                  }`}
                  alt=""
                  className={props.itemName ? "album" : "post-paths"}
                  onClick={() => {
                    props.onMovieClick(item);
                  }}
                />
              </div>
            );
          })}
        </div>
      ) : (
        <div className="errorTitle">
          <p>No results were found</p>
        </div>
      )}
    </div>
  );
};

export default MovieTrending;
