import React from "react";
import "../../Style_ProjectReact/Intro.css";

const Intro = (prop) => {
  return (
    <div className="section-movie">
      <img
        src={`https://image.tmdb.org/t/p/original${prop.items.backdrop_path}`}
        alt="Album"
        className="sora"
      />

      <div className="mp_video">
        <h2>{prop.items.original_name}</h2>
        <div className="fox_Button">
          <button className="btn">Play</button>
          <button className="btn-1">My List</button>
        </div>

        <p>{prop.items.overview}</p>
      </div>
    </div>
  );
};

export default Intro;
