import React from "react";

const ResultList = (props) => {
  return (
    <div>
      {props.onGetItems ? (
        <div className="list-movie-search">
          {props.onGetItems.map((items) => {
            return (
              <img
                src={`https://image.tmdb.org/t/p/original${items.poster_path}`}
                alt=""
              />
            );
          })}
        </div>
      ) : (
        <div>No Movies</div>
      )}
    </div>
  );
};

export default ResultList;
