import React from "react";
import "../../Style_ProjectReact/Movie-list.css";
import MovieDetail from "../MovieDetail.js/MovieDetail";
import MovieTrending from "./ExpenseList/MovieTrending";
import { useState } from "react";
import { Fragment } from "react";

const MovieList = (props) => {
  const [selectedMovie, setSelectedMovie] = useState();

  //  nhận obj Movie làm tham số để hiển thị dữ liệu bộ phim khi use Click vào
  function handleMovieClick(movie) {
    selectedMovie?.id === movie.id
      ? setSelectedMovie(undefined)
      : setSelectedMovie(movie); // {...}
  }

  return (
    <Fragment>
      <div className="list-movie">
        <MovieTrending
          items={props.movies}
          onMovieClick={handleMovieClick}
          itemName={props.titles}
        />

        <div className="show_cons_2">
          {selectedMovie && <MovieDetail selectedMovie={selectedMovie} />}
        </div>
      </div>
    </Fragment>
  );
};

export default MovieList;
