import React, { useCallback, useEffect, useState } from "react";
import Intro from "../Banner/Intro";

const requests = {
  fetchTrending: `https://api.themoviedb.org/3/trending/all/week?api_key=379f3ed664014cbf27c8eafce6dedfeb&language=en-US`,
  fetchNetflixOriginals: `https://api.themoviedb.org/3/discover/tv?api_key=379f3ed664014cbf27c8eafce6dedfeb&with_network=123`,
  fetchTopRated: `https://api.themoviedb.org/3/movie/top_rated?api_key=379f3ed664014cbf27c8eafce6dedfeb&language=en-US`,
  fetchActionMovies: `https://api.themoviedb.org/3/discover/movie?api_key=379f3ed664014cbf27c8eafce6dedfeb&with_genres=28`,
  fetchComedyMovies: `https://api.themoviedb.org/3/discover/movie?api_key=379f3ed664014cbf27c8eafce6dedfeb&with_genres=35`,
  fetchHorrorMovies: `https://api.themoviedb.org/3/discover/movie?api_key=379f3ed664014cbf27c8eafce6dedfeb&with_genres=27`,
  fetchRomanceMovies: `https://api.themoviedb.org/3/discover/movie?api_key=379f3ed664014cbf27c8eafce6dedfeb&with_genres=10749`,
  fetchDocumentaries: `https://api.themoviedb.org/3/discover/movie?api_key=379f3ed664014cbf27c8eafce6dedfeb&with_genres=99`,
  fetchSearch: `https://api.themoviedb.org/3/search/movie?api_key=379f3ed664014cbf27c8eafce6dedfeb&language=en-US`,
};

const Banner = () => {
  const [movie, setMovie] = useState([]);
  const movieHandler = useCallback(async () => {
    const fetchHandler = await fetch(requests.fetchNetflixOriginals);
    const data = await fetchHandler.json();

    const renderList =
      data.results[Math.floor(Math.random() * data.results.length - 1)];

    setMovie(renderList);
  }, []);

  useEffect(() => {
    movieHandler();
  }, [movieHandler]);

  return <Intro items={movie} />;
};

export default Banner;
