import React from "react";
import IconSearch from "../NavBar/IconSearch";
import "../../Style_ProjectReact/Navbar.css";
import { useEffect } from "react";
import { useState } from "react";
import { Link } from "react-router-dom";

const Navbar = () => {
  //  Navbar scrolling effect
  const [scrollNavBar, setScrollNavBar] = useState("inner-space");

  useEffect(() => {
    const scrollBar = () => {
      if (window.scrollY > 100) {
        setScrollNavBar("blackScroll");
      } else {
        setScrollNavBar("inner-space");
      }
    };

    window.addEventListener("scroll", scrollBar);

    return () => {
      window.removeEventListener("scroll", scrollBar);
    };
  }, []);

  // //  chuyển hướng đến Search
  // const goSearch = () => {
  //   window.location.replace("/search"); // khong dung path the nay
  // };

  // //  reload trang chủ
  // const goHome = () => {
  //   window.location.replace("/");
  // };

  ////////////////////////////////////////////////////////

  return (
    <div className="Table_of_contents">
      <div className={scrollNavBar}>
        <Link to="/">
          <h3>Movie App</h3>
        </Link>
        <Link to="/search">
          <IconSearch />
        </Link>
      </div>
    </div>
  );
};

export default Navbar;
