import React, { useRef } from "react";
import { useState } from "react";
import ResultList from "./ResultList";

const requests = {
  fetchSearch: `https://api.themoviedb.org/3/search/movie?api_key=379f3ed664014cbf27c8eafce6dedfeb&language=en-US`,
};

const SearchForm = (props) => {
  const [searchItem, setSearchItem] = useState();
  const nameInputRef = useRef();

  const formSubmitChange = (event) => {
    event.preventDefault();

    const itemSearch = async () => {
      const response = await fetch(
        `${requests.fetchSearch}&query=${nameInputRef.current.value}`
      );
      const data = await response.json();
      return setSearchItem(data.results);
    };
    itemSearch();
  };

  return (
    <div className="receptacle">
      <div className="form-control-search">
        <div className="card-form">
          <form className="form-control" onSubmit={formSubmitChange}>
            <div className="form-item-1">
              <input type="text" placeholder="Movie name" ref={nameInputRef} />
              <i className="fa-solid fa-magnifying-glass"></i>
            </div>

            <div className="btn-form">
              <button>RESET</button>
              <button className="blueBtn">SEARCH</button>
            </div>
          </form>
        </div>
      </div>

      <div className="card-movie-search">
        <h2>Search Result</h2>

        <div className="box1">
          <ResultList onGetItems={searchItem} />
        </div>
      </div>
    </div>
  );
};

export default SearchForm;
