import React, { useState } from "react";
import "../../Style_ProjectReact/MovieDetail.css";
import { useEffect } from "react";
import axios from "axios";
import YouTube from "react-youtube";

const MovieDetail = (props) => {
  const [movie, setMovie] = useState({});

  const getMovie = async () => {
    try {
      const result = await axios.get(
        `https://api.themoviedb.org/3/movie/${props.selectedMovie.id}/videos?api_key=379f3ed664014cbf27c8eafce6dedfeb&language=en-US`
      );
      const res = result?.data?.results;

      if (
        res &&
        res.length > 0 &&
        res.filter(
          (re) =>
            re.site === "YouTube" &&
            (re.type === "Trailer" || re.type === "Teaser")
        ).length > 0
      ) {
        setMovie(res[0]);
      }
    } catch {
      setMovie({});
    }
  };

  useEffect(() => {
    getMovie();
  }, [props.selectedMovie.id]);

  return (
    <div className="movie_info">
      <div className="Info_component">
        <div className="col_box_1">
          <h2>
            {movie?.key
              ? props.selectedMovie.original_title
              : props.selectedMovie.original_name}
          </h2>

          <div className="evaluate">
            <p>
              Release Date:{" "}
              <span>
                {movie?.key
                  ? props.selectedMovie.release_date
                  : props.selectedMovie.first_air_date}
              </span>
            </p>

            <p>
              Vote: <span>{props.selectedMovie.vote_average}</span>
            </p>
          </div>

          <div className="para">{props.selectedMovie.overview}</div>
        </div>

        <div className="col_box_2">
          {movie?.key ? (
            <YouTube
              className="clip"
              videoId={movie?.key}
              opts={{ playerVars: { autoplay: 0 } }}
            />
          ) : (
            <div className="boxAlbum">
              <img
                src={`https://image.tmdb.org/t/p/original/${props.selectedMovie.backdrop_path}`}
                alt=""
              />
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default MovieDetail;
